import Dropdown from "./modules/dropdown";
import Sidebar from "./modules/sidebar";

document.addEventListener("DOMContentLoaded", function () {
   const dropdownHeader = new Dropdown('.header__dropdown-btn', '.header__dropdown');
   dropdownHeader.init();
   
   const sidebarMenu = new Sidebar(
       '.sidebar-menu__btn', 
       '.sidebar-menu__overlay', 
       '.sidebar-menu', 
       '.sidebar-menu .sidebar-list__link'
   );
   sidebarMenu.init();
   
   const sidebarCallback = new Sidebar(
       '.sidebar-callback__btn', 
       '.sidebar-callback__overlay', 
       '.sidebar-callback', 
       '.sidebar-callback .sidebar-list__link'
   );
   sidebarCallback.init();

   $(".reviews-carousel").owlCarousel({
      nav: true,
      dots: false,
      navText: ["<svg width=\"50\" height=\"48\" viewBox=\"0 0 50 48\" fill=\"none\" xmlns=\"http://www.w3.org/2000/svg\">\n" +
      "<path id=\"Arrow\" fill-rule=\"evenodd\" clip-rule=\"evenodd\" d=\"M19.069 29.7614L13.6069 24.5761C13.2717 24.2579 13.2717 23.7421 13.6069 23.4239L19.069 18.2386C19.4041 17.9205 19.9476 17.9205 20.2828 18.2386C20.6179 18.5568 20.6179 19.0727 20.2828 19.3909L16.2858 23.1852L35.957 23.1852L35.957 24.8148L16.2858 24.8148L20.2828 28.6091C20.6179 28.9273 20.6179 29.4432 20.2828 29.7614C19.9476 30.0795 19.4041 30.0795 19.069 29.7614Z\" fill=\"white\"/>\n" +
      "</svg>\n","<svg width=\"50\" height=\"48\" viewBox=\"0 0 50 48\" fill=\"none\" xmlns=\"http://www.w3.org/2000/svg\">\n" +
      "<path id=\"Arrow\" fill-rule=\"evenodd\" clip-rule=\"evenodd\" d=\"M30.2435 18.2386L35.7056 23.4239C36.0408 23.7421 36.0408 24.2579 35.7056 24.5761L30.2435 29.7614C29.9084 30.0795 29.3649 30.0795 29.0297 29.7614C28.6946 29.4432 28.6946 28.9273 29.0297 28.6091L33.0267 24.8148H13.3555V23.1852H33.0267L29.0297 19.3909C28.6946 19.0727 28.6946 18.5568 29.0297 18.2386C29.3649 17.9205 29.9084 17.9205 30.2435 18.2386Z\" fill=\"white\"/>\n" +
      "</svg>\n"],
      responsive : {
         // breakpoint from 0 up
         0 : {
            items: 1,
            nav: false,
            autoWidth: false
         },
         // breakpoint from 768 up
         767 : {
            items: 2,
            nav: true,
         },
         // breakpoint from 1200 up
         1199 : {
            items: 3,
         }
      }
   });

   $('[data-fancybox-callback]').fancybox({
      animationDuration : 600,
      animationEffect   : 'slide-in-out',
      touch : false,
   });

   $('[data-fancybox-reviews]').fancybox({
      animationDuration : 600,
      animationEffect   : 'slide-in-up',
      touch : false,
   });
});